import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {actionTypes} from '@servicenow/ui-core';
import {layerEvents} from '@servicenow/library-enhanced-utils';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './_now-modeless-dialog.scss';

const {
	COMPONENT_CONNECTED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

const getComputedDimension = (styles, property) => {
	return Math.floor(
		parseFloat(styles.getPropertyValue(property).replace('px', ''))
	);
};

const setHeaderSlotsState = (host, updateState) => {
	const hasHeaderSlots =
		Boolean(host.querySelector(":scope > [slot='heading']")) ||
		Boolean(host.querySelector(":scope > [slot='actions']"));
	updateState({hasHeaderSlots});
};

const addDialogToStack = (id, dispatch) => {
	layerEvents.add({
		id,
		events: ['keydown'],
		callback: (event) => {
			if (event.key !== 'Escape') return;

			dispatch(() => {
				return {
					type: 'NOW_MODELESS_DIALOG#OPENED_SET',
					payload: {value: false}
				};
			});
		}
	});
};

const view = ({hasHeaderSlots, properties: {opened, canDrag}}) => {
	return (
		<div
			class={{
				'now-modeless-dialog': true,
				'is-open': opened
			}}
			role="dialog"
			aria-modal="false"
			aria-hidden={opened ? 'false' : 'true'}
			aria-labelledby="now-modeless-dialog-heading">
			<div
				class={{
					'now-modeless-dialog-header': true,
					'can-drag': canDrag,
					'-border-block-end': hasHeaderSlots
				}}>
				<slot name="heading" id="now-modeless-dialog-heading" />
				<slot name="actions" />
			</div>
			<div className="now-modeless-dialog-content">
				<slot name="content" />
			</div>
			<div class={{'now-modeless-dialog-footer': true, 'can-drag': canDrag}}>
				<slot name="footer" />
			</div>
		</div>
	);
};

/**
 * A modeless dialog element to be placed fixed on a page.
 * It has several slots to provide a wide range of customization
 * of its header, actions, content and footer.
 *
 * To fully utilize this component, you will need:
 * 1. A way to track the `opened` state of the dialog. E.g., an enclosing component.
 * 2. DIVs around the trigger and `now-modeless-dialog` element for fixed positioning.
 * 3. Styles to position the dialog within the wrapping DIVs.
 *
 * Example view, with wrappers and state:
 *
 * ```jsx
 * // sn-modeless-dialog.js
 *
 * <div className="sn-modeless-dialog-overlay">
 * 	<div className="sn-modeless-dialog-position-container">
 * 		<now-button-circular
 *			className="sn-modeless-dialog-trigger"
 *			config-aria={{'aria-label': "Open dialog"}}
 *			icon="chat-fill"
 *			tooltip-content="Open dialog"
 *			aria-expanded={state.opened ? 'true' : 'false'}
 *			on-click={() => updateState({open: !state.opened})}
 *		/>
 * 		<now-modeless-dialog can-drag opened={state.opened}>
 * 			<h3 slot="heading">
 *				<!-- heading content -->
 *			</h3>
 *			<div slot="actions">
 *				<!-- buttons handle close, minimize, make full screen, etc -->
 *			</div>
 *			<div slot="content">
 *				<!-- body content -->
 *			</div>
 *			<div slot="footer">
 *				<!-- footer for actions, metadata, etc -->
 *			</div>
 * 		</now-modeless-dialog>
 * 	</div>
 * </div>
 * ```
 *
 * Example styles for positioning:
 *
 * ```scss
 * .sn-modeless-dialog-overlay {
 * 	position: fixed;
 * 	top: 0;
 * 	right: 0;
 * 	bottom: 0;
 * 	left: 0;
 * 	z-index: $now-global-layer--notify;
 * 	pointer-events: none;
 * }
 *
 * .sn-modeless-dialog-position-container {
 * 	position: absolute;
 * 	top: 0;
 * 	right: 0;
 * 	width: 380px;
 * 	height: auto;
 * 	margin: 20px;
 * 	pointer-events: none;
 * }
 * ```
 *
 * Additionally, you can add `overflow-y: auto;` to the content slot
 * element to allow its content to be scrollable.
 *
 * @seismicElement now-modeless-dialog
 * @summary A non-modal dialog element.
 */
createEnhancedElement('now-modeless-dialog', {
	properties: {
		/**
		 * Configures the dialog window to be draggable. If enabled, mousedown events
		 * on the header and/or footer (if slots are given) will dispatch the
		 * `NOW_MODELESS_DIALOG#MOUSEDOWN` action.
		 * @type {boolean}
		 */
		canDrag: {
			default: false,
			schema: {type: 'boolean'}
		},
		/**
		 * Indicates if the component is currently opened. Use `manage-opened` to override
		 * the default behavior and handle the `NOW_MODELESS_DIALOG#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		opened: {
			default: false,
			manageable: true,
			schema: {type: 'boolean'}
		}
	},
	slots: {
		/**
		 * Slot to add heading content to the top of the dialog.
		 * Use padding utility classes to add spacing.
		 */
		heading: {},
		/**
		 * Slot to add dialog controls to close, minimize, or expand.
		 * Use padding utility classes to add spacing.
		 */
		actions: {},
		/**
		 * Slot to add body content to the dialog.
		 * Use padding utility classes to add spacing.
		 */
		content: {},
		/**
		 * Slot to add footer content, such as a toolbar or text input, to the dialog.
		 * Use padding utility classes to add spacing.
		 */
		footer: {}
	},
	dispatches: {
		/**
		 * Dispached when the header or footer areas are clicked (assuimng slots are
		 * present) while the `can-drag` property is present.
		 * @type {{positions: {mouse: {X: number, Y: number}, offset: {left: number, top: number}}, dimensions: {width: number, height: number}}}
		 */
		'NOW_MODELESS_DIALOG#MOUSEDOWN': {},
		/**
		 * Dispatched when the modal is dismissed by the user pressing the `Escape`
		 * key while the modal is opened. Set the `manage-opened` property to
		 * override the default behavior and handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_MODELESS_DIALOG#OPENED_SET': {}
	},
	initialState: {triggerEl: null, hasHeaderSlots: false},
	behaviors: [
		{
			behavior: focusBehavior,
			options: {trap: true}
		}
	],
	eventHandlers: [
		{
			events: ['mousedown'],
			effect({host, dispatch, properties: {canDrag}, action}) {
				if (!canDrag) return;
				const {event} = action.payload;

				const header = host.nowRootNode.querySelector(
					'.now-modeless-dialog-header'
				);
				const footer = host.nowRootNode.querySelector(
					'.now-modeless-dialog-footer'
				);
				const path = event.composedPath();

				if (!path.includes(header) && !path.includes(footer)) {
					return;
				}

				action.preventDefault();

				/**
				 * Provide initial position calculations up front so consuming
				 * applications can run their own `mousemove` event and
				 * calculate new positions relative to these:
				 *
				 * 1) Container top left corner's X and Y coordinates
				 * 2) Container left and top offsets
				 * 3) Mouse X and Y coordinates
				 */
				const panel = host.nowRootNode.querySelector('.now-modeless-dialog');
				const styles = getComputedStyle(panel);
				const rect = panel.getBoundingClientRect();
				const payload = {
					positions: {
						mouse: {
							X: Math.floor(event.pageX - pageXOffset),
							Y: Math.floor(event.pageY - pageYOffset)
						},
						offset: {
							left: Math.floor(rect.left),
							top: Math.floor(rect.top)
						}
					},
					dimensions: {
						width: getComputedDimension(styles, 'width'),
						height: getComputedDimension(styles, 'height')
					}
				};
				dispatch(() => ({type: 'NOW_MODELESS_DIALOG#MOUSEDOWN', payload}));
			}
		}
	],
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({
			host,
			properties,
			dispatch,
			action,
			updateState
		}) => {
			const {id} = action.meta;
			if (properties.opened) addDialogToStack(id, dispatch);
			// There should only be a bottom border on the heading/actions
			// slots wrapper when either is present. We detect them in the light
			// dom, set state, and add a class to the wrapper. This is necessary
			// because there's no way to add a style to a parent from a child
			// selector in CSS.
			setHeaderSlotsState(host, updateState);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({dispatch, action: {meta, payload}}) => {
			if (payload.name === 'opened') {
				if (payload.value) {
					addDialogToStack(meta.id, dispatch);
				} else {
					layerEvents.remove(meta.id);
				}
			}
		},
		[COMPONENT_DISCONNECTED]: ({action, properties: {opened}}) => {
			if (opened) layerEvents.remove(action.meta.id);
		}
	},
	styles,
	view
});
